import React, { Component } from 'react';
import axios from "axios";
import PropTypes from "prop-types";
import { connect } from "react-redux";
import { Link } from "react-router-dom";

import { 
  getExpt,
  sendExpt
} from "../actions/dataActions";

import './Slider.css'

class Experiment extends Component {
  constructor(props) {
    super(props);
    this.state = {
      value: 0
    }

    this.onChange = this.onChange.bind(this);
    this.onSubmit = this.onSubmit.bind(this);
  }

  componentDidMount() {
    this.getData();

    const username = this.props.match.params.username;
    const expt = this.props.match.params.expt;
    if (!this.props.expt.participantID) {
      alert("Please enter your unique ID");
      this.props.history.push("/" + username + "/" + expt);
    }
  }

  onChange(e) {
    this.setState({ [e.target.name]: e.target.value })
  }

  onSubmit() {
    const finalObj = { 
      "PartID": this.props.expt.participantID,
      "sliderVal": this.state.value
    };
    const username = this.props.match.params.username;
    const expt = this.props.match.params.expt;
    this.props.sendExpt(username, expt, finalObj);
    this.props.history.push("/");
  }

  getData() {
    const username = this.props.match.params.username;
    const studyName = this.props.match.params.expt.split("-")[0];
    const exptName = this.props.match.params.expt.split("-")[1];
    this.props.getExpt(username, studyName, exptName);
  }

  displayExpt() {
    const expt = this.props.expt.exptToDisplay;
    const allKeys = Object.keys(expt);
    const questionKeys = allKeys.filter(k =>
      k != "userID" && k != "exptName" && k != "count" && k != "type");
    const questionKey = questionKeys[0];
    if (expt[questionKey]) {
      if (expt[questionKey]["Type"] == "slider") {
        const lowRange = expt[questionKey]["lowRange"];
        const highRange = expt[questionKey]["highRange"];
        const question = expt[questionKey]["Question"];
        return (
          <div className="container">
            {question} <br/>
            <input 
              type="range" 
              min={lowRange} 
              max={highRange}
              name="value"
              value={this.state.value}
              onChange={this.onChange}
            />
            {this.state.value}
            <br/>
            <input type="submit" className="btn" onClick={this.onSubmit}/>
          </div>
        )
      }
    }
    // return questionKeys.map(k => {
    //   if (expt[k]) {
    //     if (expt[k]["Type"] == "slider") {
    //       const lowRange = expt[k]["lowRange"];
    //       const highRange = expt[k]["highRange"];
    //       const question = expt[k]["Question"];
    //       return (
    //         <div className="container">
    //           <form onSubmit={this.onSubmit}>
    //             {question} <br/><br/>
    //             <input 
    //               type="range" 
    //               min={lowRange} 
    //               max={highRange}
    //               name="value"
    //               value={this.state.value}
    //               onChange={this.onChange}
    //             />
    //             {this.state.value}
    //             <br/>
    //             <input type="submit" className="btn"/>
    //           </form>
    //         </div>
    //       );
    //     }
    //   }
    // })
  }

  sendData(finalData) {
    const username = this.props.match.params.username;
    const exptName = this.props.match.params.expt;
    const API_URL = 'https://test-api-615.herokuapp.com/api/feedback/' +
      username + '/' + exptName;
    axios
      .post(API_URL, finalData)
      .then(res => {
        console.log(res);
        alert("You have successfully submitted your response.");
      })
  }

  render() {
    const exptName = this.props.match.params.expt;
    const participant = this.props.expt.participantID;
    return (
      <div className="container">
      Experiment name: <br/>
      <b>{exptName}</b>
      <br/>
      Participant ID: <br/>
      <b>{participant}</b>
      <br/><br/>
      {this.displayExpt()}
    </div>
    )
  }
}

Experiment.propTypes = {
  getExpt: PropTypes.func.isRequired,
  expt: PropTypes.object.isRequired,
  participantID: PropTypes.string.isRequired,
  sendExpt: PropTypes.func.isRequired
};

const mapStateToProps = state => ({
  expt: state.expt,
  participantID: state.participantID
})

export default connect(
  mapStateToProps,
  { getExpt, sendExpt }
)(Experiment);
